.overlay {
  position: fixed; /* Фиксируем затемнение поверх страницы */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9); /* Полупрозрачный черный фон */
  z-index: 996; /* Под меню, но над остальным контентом */
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.overlay_visible {
  visibility: visible;
  opacity: 1;
  z-index: 996;
}

